.displayweather {
    margin-top: 20px;
    display: flex;
    flex-flow: column nowrap;
  }
  

  .tempAndpicture{
    display: flex;
    justify-content: flex-start;
    padding-top: 15px;
  }

  .displayweather .maincard {
    width: 500px;
    margin-top: 20px;
    align-self: center;
    padding: 50px 50px 10px 50px;
    margin-bottom: 50px;
    border-radius: 20px;
    position: relative;
    background: rgb(252, 226, 226);
    border: 1px solid black;
  }
  
  .displayweather .maincard .cardtitle {
    position: absolute;
    top: 10px;
    left: 10px;
    font-size: 1.5rem;
  }
  
  .displayweather .maincard .cardsubtitle {
    position: absolute;
    left: 10px;
    color: grey;
    margin-top: -8px;
  }

  .displayweather h1 {
    font-size: 5rem;
  }
  
  .displayweather .weather-icon {
    width: 100px;
    /*position: absolute;
    top: 50px;*/
  }
  
  .displayweather .weather-main {
    position: absolute;
    top: 120px;
    left: 310px;
    font-weight: 500;
    font-size: 1.2rem;
  }
  
  .displayweather .weather-description {
    position: absolute;
    left: 10px;
    font-size: 1.4rem;
  }

  .displayweather .weatherdetails {
    margin-left: 50px;
    margin-right: 50px;
  
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
    grid-gap: 20px;
  }
  
  .displayweather .weatherdetails .section1 {
    justify-self: right;
    width: 300px;
  }
  
  h4 {
    text-align: left;
    margin-right: 30px;
    font-size: 1.2rem;
  }
  
  td {
    border-bottom: 2px dashed grey;
  }
  
  .displayweather .weatherdetails span {
    font-size: 1.2rem;
  }